















































































































































































h1 {
  text-align: left;
  font-size: 16px;
  font-weight: bold;
}

h3 {
  text-align: left;
}

.commentBox {
  margin-top: 16px;
  margin-bottom: 16px;
  text-align: left;
  clear: both;
}
.date{
  color: #555555;
  font-weight: normal;
  font-size: 75%;
  -webkit-text-decoration: italic;
          text-decoration: italic;
  text-align: left;
}
.comment {
  padding-left: 64px;
  text-align: left;
}
.name {
  padding-left: 64px;
  color: #2196F3;
  font-weight: bold;
  padding-bottom: 4px;
  text-align: left;
}
.avatar img {
  width: 48px;
  height: 48px;
  float: left;
  border-radius: 4px;
}
body{
  background: none transparent;

}

input {
  background: black;
  border: 1px solid #555555;
  color: #CCCCCC;
}

button {
  background: black;
  border: 1px solid #555555;
  color: #CCCCCC;
}

body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #CFD8DC;

}

.postCommentContainer {
  border: 2px solid #0D47A1;
  background: #42A5F5;
  padding: 6px;
  border-radius: 3px;
}

textarea, .postCommentContainer, .postCommentToolbar {
  width: 100%;
  background: #212121;
  padding: 0;
  color: white;
}

textarea{
  padding: 6px;
  border: 0;
  border-radius: 3px;
  background: none;
}

.postCommentToolbar {
  text-align: right;
  background: #0D47A1;
}

.postCommentContainer button {
  background: #0D47A1;
  margin: 4px;
  padding: 4px;
  border: 2px solid #2196F3;
  border-radius: 4px;
  color: #2196F3;
  font-weight: bold
}
